var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('b-container',{staticClass:"mt-5"},[_c('b-row',[_c('b-col',{attrs:{"lg":"4"}},[_c('div',{staticClass:"d-flex justify-content-center"},[_c('img',{attrs:{"src":"https://images1.loopnet.com/i2/w1Dbm5Q4gGIP8HtykSJpP3PFYD4jCGWVvWnvD1eWdNQ/118/image.jpg"}})])]),_c('b-col',{attrs:{"lg":"8"}},[_c('h1',{staticClass:"text-center"},[_vm._v("Karyne Morris")]),_c('p',[_vm._v(" Karyne Morris has always held an entrepreneurial leadership role, and has worked in the Entertainment Industry for over 30 years. In 1989, she became the Owner and CEO of ACTA (Academy Casting Team and Associates), based in North Hollywood, California. ACTA casted over 100 Hip Hop/Rap Videos from 1989-1991 working with groups like ABC, Bel Bev Devo, Keith Sweat and Boyz to Men. ACTA produced a transitional known RAP Group’s POW “Expression of RAP.” In 1991, Karyne bought out her partner and expanded the company to film and TV. Her relationship with Maximum Entertainment allowed her to successfully cast a number of their projects, including Disney’s “Honey I Blew Up the Baby,” Steven King’s “The Stand,” Jimmy Zeilinger’s “Little Sister,” Vista Street Ent. “Witchcraft II-IV” and Playboy’s TV/MTV “True Confessions and Fantasy.” ")]),_c('p',[_vm._v(" Following a move to Las Vegas in 1994 with plans to start a family, Karyne changed gears and opened her own Commercial and Residential Real Estate firm, KB Morris Real Estate. In 2008, she received a request from Blumhouse Productions’ Jason Blum, Producer of Paranormal Activity, to cast “Area 51,” Oren Peli’s 2nd movie due to release October 2012. This casting project was followed by a capital raise for a Michael Jackson project, “Never Can Say Goodbye.” As she continued to be approached by many of her old colleagues for casting and funding and with the economy changing the Real Estate Industry, Karyne felt it was time to shift back to her passion in the Entertainment Industry. ")]),_c('p',[_vm._v(" Recently, Karyne has branched out to the Greater Los Angeles Area, joining forces with the Nartey Group in Beverly Hills, which specializes in Celebrity, Sport, and Luxury Clientele. Over the years, Karyne has found that integrating her Entertainment and Real Estate acumen has been a successful model for Venture Capital in the Entertainment Industry. From network connections to financing, Karyne has found opportunities in both Real Estate and Production. ")]),_c('p',[_vm._v(" Philanthropy and Entertainment fit hand-in-hand for Karyne. She spends some time working with the Veterans Village association in conjunction with Arnold Stalk by facilitating locations and investors to help the Veterans live a better way of life. Karyne enjoys assisting at the American Cancer Society weekly. Karyne also volunteers her time with Make a Wish foundation and First Tee helping underprivileged children get into sport activities they cannot afford. ")])])],1)],1)}
